import React from 'react';

const Skypeqr = () => {
    const hidden = () => {
        const skypeQrCode = document.querySelector('.skype-qr-code');
        skypeQrCode.style.opacity = '0';
        skypeQrCode.style.transform = 'translate(1000px, 50%)';
    }
    return (
        <div className='skype-qr-code'>
            <img onClick={hidden} className='img-skype-qrcode' src="./assets/img/qrskype.jpg" alt="QR code du skype de Sebastien Guittard - Developpeur React" />
            <i class="closed-qr-code fa-solid fa-xmark"></i>
        </div>
    );
};

export default Skypeqr;