import React from 'react';
import { NavLink } from 'react-router-dom';

const Navigation = () => {
    return (
        <div className='navigation'>
            <ul>
                <NavLink to="/" className={(nav) => (nav.isActive) ? "nav-active hover" : "hover"}>
                    <li>accueil</li>
                </NavLink>
                <li className='nav-portfolio'>
                    portfolio
                    <ul className="nav-projects">
                        <NavLink to="/projet-resto" className={(nav) => (nav.isActive) ? "nav-active hover" : "hover"}>
                            <li>projet Resto</li>
                        </NavLink>
                        <NavLink to="/projet-yoga" className={(nav) => (nav.isActive) ? "nav-active hover" : "hover"}>
                            <li>projet Yoga</li>
                        </NavLink>
                        <NavLink to="/projet-country" className={(nav) => (nav.isActive) ? "nav-active hover" : "hover"}>
                            <li>projet Country App</li>
                        </NavLink>
                        <NavLink to="/projet-cooking" className={(nav) => (nav.isActive) ? "nav-active hover" : "hover"}>
                            <li>projet Cooking App</li>
                        </NavLink>
                    </ul>
                </li>
                <NavLink to="/contact" className={(nav) => (nav.isActive) ? "nav-active hover" : "hover"}>
                    <li>contact</li>
                </NavLink>
            </ul>

        </div>
    );
};

export default Navigation;