import React, { useRef, useState } from 'react';
import emailjs from "@emailjs/browser";
import { init } from "@emailjs/browser";
import ReCAPTCHA from 'react-google-recaptcha';
init(process.env.id);

const ContactForm = () => {
    const [valueButton, setValueButton] = useState("Envoyer");
    const formMess = document.querySelector('.formMessage');
    const formulaire = document.querySelector('.form-content')
    const captcha = useRef(null);
    const form = useRef();

    const onChange = () => {
        if (captcha.current.getValue()) {
            console.log("Captcha valider 1");
        }
    };

    const sendEmail = (e) => {
        e.preventDefault();
        setValueButton("Wait...");
        if (captcha.current.getValue()) {
            emailjs
                .sendForm(
                    process.env.REACT_APP_ID,
                    process.env.REACT_APP_TEMPLATEID,
                    form.current,
                    process.env.REACT_APP_PUBLICKEY
                )
                .then((result) => {
                    console.log(result.text);
                    form.current.reset();
                    formMess.innerHTML = "<p class='success'>Message envoy !</p>";
                    setValueButton("Envoyer");
                    setTimeout(() => {
                        formulaire.innerHTML = "<h3>Merci pour votre message</h3>";
                        formMess.innerHTML = "<p style='transform: translate(0px,0px);' class='success'>Message envoy !</p>";
                    }, 2000);
                }, (error) => {
                    console.log(error.text);
                    formMess.innerHTML = "<p class='error'>Une erreur s'est produite, veuillez resayer</p>";
                    setValueButton("Error");
                    setTimeout(() => {
                        formMess.innerHTML = "";
                        setValueButton("Envoyer");
                    }, 2000);
                });
        } else {
            setValueButton("Attention...");
            formMess.innerHTML = "<p class='error'>Veuillez valider le Captcha</p>";
            setTimeout(() => {
                formMess.innerHTML = "";
                setValueButton("Envoyer");
            }, 2000);
        }
    };

    return (
        <div className="form-container">
            <h2>contactez-moi</h2>
            <form ref={form} onSubmit={sendEmail} className="form-content">
                <label>nom</label>
                <input type="text" name="name" required autoComplete="off" id="name" />
                <label>Email</label>
                <input
                    type="email"
                    name="email"
                    required
                    autoComplete="off"
                    id="email"
                />
                <label>Message</label>
                <textarea name="message" id="mess" />
                <ReCAPTCHA ref={captcha} sitekey={process.env.REACT_APP_V2_CAPTCHA_SITE_KEY} onChange={onChange} />
                <input type="submit" value={valueButton} className="hover button" />
            </form>
            <div className="formMessage"></div>
        </div>
    );
};

export default ContactForm;