import React from 'react';
import Mouse from '../components/Mouse';
import Navigation from '../components/Navigation';
import Logo from '../components/Logo';
import Buttons from '../components/Buttons';
import ContactForm from '../components/ContactForm';
import { CopyToClipboard } from "react-copy-to-clipboard";
import SocialNetwork from '../components/SocialNetwork';
import Skypeqr from '../components/Skypeqr';
import { motion } from "framer-motion";


const Contact = () => {
    const pageTransition = {
        in: {
            opacity: 1,
            x: 0,
        },
        out: {
            opacity: 0,
            x: 300,
        }
    }

    const visibilityCode = () => {
        const skypeQrCode = document.querySelector('.skype-qr-code');
        skypeQrCode.style.opacity = '1';
        skypeQrCode.style.transform = 'translate(-50%, 0%)';
    }

    return (
        <main>
            <Mouse />
            <Skypeqr />
            <motion.div className="contact" initial="out" animate="in" exit="out" variants={pageTransition} transition={{ duration: 0.4 }}>
                <Navigation />
                <Logo />
                <ContactForm />
                <div className="contact-infos">
                    <div className="address">
                        <div className="content">
                            <h4>adresse</h4>
                            <p></p>
                            <p>06000 Nice</p>
                        </div>
                    </div>
                    <div className="phone">
                        <div className="content">
                            <h4>Skype</h4>
                            <CopyToClipboard text="https://sebastiendev.fr/assets/img/qrskype.jpg" className="hover" >
                                <p
                                    style={{ cursor: "pointer" }}
                                    className="clipboard"
                                    onClick={visibilityCode}
                                >QR Code</p>
                            </CopyToClipboard>
                        </div>
                    </div>
                    <div className="email">
                        <div className="content">
                            <h4>email</h4>
                            <CopyToClipboard text="fsangancy@gmail.com" className="hover" >
                                <p
                                    style={{ cursor: "pointer" }}
                                    className="clipboard"
                                    onClick={() => alert("Email copi !")}
                                >contact@sebastiendev.fr</p>
                            </CopyToClipboard>
                        </div>
                    </div>
                    <SocialNetwork />
                    <div className="credits">
                        <p>Sebastien - 2022</p>
                    </div>
                </div>
                <Buttons left={"/projet-cooking"} />
            </motion.div>
        </main >
    );
};

export default Contact;